body,
ul,
li {
  margin: 0;
  padding: 0;
  text-align: center;
}
/* Header */
.header {
  background-color: #ffffff;
  line-height: 4rem;
  width: 100%;
  z-index: 1000;
  position: fixed;
  box-shadow: 0px 0px 10px 0.5px gray;
}
.home {
  float: left;
  padding-left: 20px;
  font-size: 1.5rem;
}
.nav {
  float: right;
  padding-left: 10px;
  padding-right: 10px;
}
.nav > * {
  color: #4d5058;
}
.active {
  color: #0090fc;
}
/* Home */
.home-wrap {
  z-index: 900;
  height: 100vh;
  background-color: #f2f9ff;
}
.home-content {
  margin: 0 auto;
  text-align: center;
}
.home-title {
  padding-top: 15vh;
  font-size: 2rem;
}
.home-icon-wrap {
  padding-top: 15vh;
  display: flex;
  justify-content: space-evenly;
}
.home-icon {
  float: left;
  padding: 10px;
  width: 20%;
}
.home-icon-text {
  padding-top: 5px;
}
.home-sub-title {
  padding-top: 15vh;
  font-size: 2rem;
}
.custom-shape-divider-bottom-1626948820 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
  z-index: 900;
}

.custom-shape-divider-bottom-1626948820 svg {
  position: relative;
  display: block;
  width: calc(146% + 1.3px);
  height: 18vh;
}

.custom-shape-divider-bottom-1626948820 .shape-fill {
  fill: #0090fc;
}
/* Skills */
.skiils-wrap {
  margin: 0 auto;
  padding-bottom: 8vh;
}
.nav-title {
  font-size: 2rem;
  padding-top: 8vh;
  padding-bottom: 8vh;
  margin: 0 auto;
}
.nav-title > p::before {
  content: "";
  display: block;
  margin: 0 auto;
  width: 20px;
  border-top: 3px solid #0090fc;
}

.skills-image-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.skills-image-wrap > img {
  height: 11vh;
  object-fit: cover;
  padding: 10px;
}
/* Projects */

.projects-wrap {
  z-index: 900;
  background-color: #f2f9ff;
}
.project-row {
  display: flex;
  flex-wrap: wrap;
  max-width: 1080px;
  margin: 0 auto;
  padding-bottom: 30px;
}
.project-content-youtube {
  margin: 0 auto;
  width: 50%;
  height: 440px;
}
.project-content-carousel {
  margin: 0 auto;
  width: 50%;
  height: 440px;
}
.project-content-right {
  margin: 0 auto;
  width: 540px;
  height: 440px;
  padding-top: 50px;
}
.project-title {
  text-align: center;
  margin: 0 auto;
  font-size: 2rem;
  font-weight: bolder;
}

.project-subTitle {
  font-size: 1rem;
  padding: 5px;
  font-weight: lighter;
}
.project-desc-title {
  line-height: 20px;
  font-size: 1.4rem;
  font-weight: bolder;
  padding: 10px;
}
.project-desc-content {
  width: 80%;
  margin: 0 auto;
  font-size: 0.9rem;
}
.site-icon > a > * > * {
  text-decoration: none;
  font-size: 1rem;
}
.site-icon > a {
  padding: 5px;
}
.site-icon {
  padding: 10px;
}
.youTube-link {
  height: 35vh;
}
.youTube-link > iframe {
  width: 100%;
  height: 100%;
}
.imgWrap > img {
  width: 50%;
  object-fit: cover;
  display: block;
  margin: 0 auto;
}
.slick-next::before {
  color: #0090fc;
  background-color: #0090fc;
  border-radius: 100%;
}
.slick-prev::before {
  color: #0090fc;
  background-color: #0090fc;
  border-radius: 100%;
}

/* contact */
.contact-icon-wrap {
  display: flex;
  justify-content: space-evenly;
  height: 24vh;
}
.contact-icon {
  width: 30%;
}
.contact-icon > a > * {
  text-decoration: none;
  color: #4d5058;
}
.contact-icon-desc {
  text-decoration: none;
  color: #4d5058;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 0.9rem;
  font-weight: bold;
}
/* media */
@media screen and (max-width: 768px) {
  .home {
    font-size: 1rem;
  }
  .nav {
    font-size: 0.9rem;
  }
  .contact-icon-desc {
    font-size: 0.7rem;
  }
  .project-content-youtube {
    margin: 0 auto;
    width: 100%;
    height: 100%;
  }
  .project-content-carousel {
    margin: 0 auto;
    width: 100%;
    height: 100%;
  }
  .imgWrap > img {
    width: 100%;
    object-fit: cover;
    display: block;
    margin: 0 auto;
  }
  .home-icon {
    width: 33%;
  }
  .home-sub-title {
    padding-top: 5vh;
    font-size: 1.5rem;
  }
  .home-icon-wrap {
    padding-top: 5vh;
  }
}
